const ADD_NEWS = 'ADD_NEWS';

let initialState = {
    newsData: [
        {
            id: 1,
            by: 'Petr',
            name: 'Petr changed avatar',
            publishTime: '5pm',
            img: 'data:image/jpeg;base64,'
        },
        {
            id: 2,
            by: 'Uliana',
            name: 'Uliana shared photo',
            publishTime: '10am',
            img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR0UUvRj7qeX8b4VKXDZVwEM-Ld7TEtgOr-iw&usqp=CAU'
        },
        {
            id: 3,
            by: 'Valya',
            name: 'Valya liked your post',
            publishTime: '1pm',
            img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR0UUvRj7qeX8b4VKXDZVwEM-Ld7TEtgOr-iw&usqp=CAU'
        },
    ]
}

const newsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_NEWS: {
            let newNews = {
                id: state.newsData.length + 1,
                by: 'Me',
                name: action.newNewText,
            };
            return {
                ...state,
                newsData: [...state.newsData, newNews],
            }
        }
        default:
            return state;
    }
}
export default newsReducer;

export const addNewsAC = (newNewText) => ({type: ADD_NEWS, newNewText});
